import { Button, Card, Grid } from '@mui/material';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CodeIcon from '@mui/icons-material/Code';
import PropTypes from 'prop-types';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import AppAnimate from '@crema/core/AppAnimate';
import { Fonts } from 'shared/constants/AppEnums';
import CInvoice from '../../../../../components/CInvoice';
import { LoadingButton } from '@mui/lab';

const AddOrderReportPreviewContanerUI = ({
    source,
    title,
    description,
    viewSource,
    setToggleViewSource,
    messages,
    animation,
    setAnimation,
    cartItem,
    clientInfo,
    orderSummary,
    addOrder,
    orderId,
    orderAdded,
    loading
}) => {

    return (<AppAnimate animation='transition.slideUpIn' delay={200}>
        <Card>
            <CardHeader
                sx={{
                    py: 0,
                    pb: 0,
                    px: 5,
                    display: 'flex',
                    alignItems: 'center',

                    boxSizing: 'border-box',
                    '& .MuiTypography-h5': {
                        fontSize: 14,
                        fontWeight: Fonts.BOLD,
                        marginBottom: 0.25,
                    },
                }}
                title={title}
                subheader={description}
                root={{
                    subheader: {
                        fontSize: 13,
                    },
                }}
                action={
                    source ? (
                        <Box>
                            <IconButton
                                aria-label='view code'
                                onClick={() => {
                                    if (animation) {
                                        setAnimation(!animation);
                                        setTimeout(() => setToggleViewSource(!viewSource), 400);
                                    } else {
                                        setAnimation(!animation);
                                        setToggleViewSource(!viewSource);
                                    }
                                }}
                                size='large'
                            >
                                <CodeIcon />
                            </IconButton>
                        </Box>
                    ) : null
                }
            />

            <CardContent
                sx={{ px: 4, pt: 5 }}
            >
                <Grid container spacing={4}>
                    {loading ? (
                        <Grid item xs={12}>
                            <LoadingButton loading variant='outlined'>
                                {messages['ecommerce.addproduct.loding']}
                            </LoadingButton>
                        </Grid>
                    ) : (

                        <Grid item xs={12}>
                            <Box
                                m={4}
                                display='inline'
                            >
                                <Button
                                    disabled={orderAdded}
                                    variant='contained'
                                    color='success'
                                    loading={loading}
                                    size='medium'
                                    style={{ color: '#ffffff' }}
                                    onClick={addOrder}

                                >
                                    {messages['common.saveOrder']}
                                </Button>

                            </Box>

                            <Box
                                m={4}
                                display='inline'
                            >


                            </Box>
                        </Grid>
                    )}
                    <Grid item xs={12}>
                        {orderAdded &&
                            <CInvoice orderId={orderId} clientInfo={clientInfo} products={cartItem} orderSummary={orderSummary} />
                        }
                    </Grid>
                </Grid>
            </CardContent>
        </Card>
    </AppAnimate>);
};
AddOrderReportPreviewContanerUI.propTypes = {
    component: PropTypes.any,
    source: PropTypes.any,
    title: PropTypes.any,
    description: PropTypes.any,
    viewSource: PropTypes.any,
    setToggleViewSource: PropTypes.any,
    showInvoice: PropTypes.any,
    messages: PropTypes.any,
    animation: PropTypes.any,
    setAnimation: PropTypes.any,
    cartItem: PropTypes.array,
    orderSummary: PropTypes.object,
    clientInfo: PropTypes.object,
    addOrder: PropTypes.func,
    orderId: PropTypes.any,
    orderAdded: PropTypes.bool,
    showInvoiceBT: PropTypes.bool,
    showInvoiceClicked: PropTypes.bool,
    loading: PropTypes.bool
};

export default AddOrderReportPreviewContanerUI;