import React, { useState } from 'react';
import { useIntl } from 'react-intl';

import { AppComponentHeader, AppInfoView } from '@crema';
import { Box } from '@mui/material';
import AddOrderReportPreviewContaner from './component/AddOrderReportPreviewContaner';
import { useDispatch } from 'react-redux';
import { showError, showMessage } from 'redux/actions';
import { ADD_ORDER } from 'mutation/addOrder/addOrder';
import { useMutation } from '@apollo/client';
const AddUserInformation = React.lazy(() =>
  import('./component/AddClinetInformation'),
);
const AddOrderCarts = React.lazy(() => import('./component/AddOrderCarts'));

const AddOrderAddress = React.lazy(() => import('./component/AddOrderAddress'));
const AddOrder = () => {
  const [clientFormData, setClientFormData] = useState({});
  const [addressFormData, setAddressFormData] = useState({});
  const [cartFormData, setCartFormData] = useState([]);
  const [orderSummary, setOrderSummary] = useState({});
  const { messages } = useIntl();

  const [orderAdded, setOrderAdded] = useState(false);
  const [orderId, setOrderId] = useState(false);
  const dispatcher = useDispatch();
  const [uploadOrder, { loading }] = useMutation(ADD_ORDER);

  const addOrder = async () => {
    let rez = {};
    try {
      rez = buildOrderObject(clientFormData,
        addressFormData,
        cartFormData,
        orderSummary
      );
    } catch (e) {
      dispatcher(showError("all fields are requierd"));
      return;
    };

    let validate = fieldValidation(rez);
    if (!validate) {
      console.log(validate);
      dispatcher(showError("all fields are requierd"));
      return;
    }
    await uploadOrder({
      variables: {
        order: rez,
      }
    }).then(i => {
      console.log(i.data.addOrder.orderId);
      setOrderAdded(true);
      setOrderId(i.data.addOrder.orderId);
      dispatcher(showMessage("sucssifully added"));

    });

    console.log("asdasdads");
  };

  const fieldValidation = (order) => {
    let validate = true;
    if (!order || order == {}) {
      validate = false;
    }
    if (!(order?.BilingInformtion?.firstname ||
      order?.BilingInformtion?.email ||
      order?.BilingInformtion?.phone ||
      order?.BilingInformtion?.address ||
      order?.BilingInformtion?.city ||
      order?.BilingInformtion?.house ||
      order?.BilingInformtion?.houseType ||
      order?.payedType ||
      order?.status ||
      order?.totalPrice ||
      order?.shipping ||
      order?.price ||
      order?.isPayed ||
      order?.discount
    )) {
      validate = false;
    }
    return validate;
  };
  const buildOrderObject =
    (
      clientFormData,
      addressFormData,
      cartFormData,
      orderSummary
    ) => {
      const billingInfo = {
        Region: addressFormData.address,
        city: addressFormData.city,
        company: "",
        email: clientFormData.email,
        firstname: clientFormData.fullName.split(" ")[0],
        house: addressFormData.house,
        houseType: addressFormData.houseType,
        lastname: clientFormData.fullName.split(" ")[1],
        notes: clientFormData.notes,
        phone: clientFormData.phone
      };
      const cartInfo = cartFormData.map(item => {
        return {
          id: item.id,
          name: item.name,
          price: Number(item.price),
          qty: Number(item.qty),
          sale_price: item.sale_price,
          short_desc: item.short_desc,
          slug: item.slug,
          sm_pictures: item.sm_pictures,
          sold: Number(item.sold),
          stock: Number(item.stock),
          sum: Number(item.qty) * Number(item.price),
          until: item.until
        };
      });
      return {
        BilingInformtion: billingInfo,
        CartItem: cartInfo,
        Date: new Date().toLocaleDateString(),
        couponCode: "",
        discount: Number(orderSummary.discount),
        isPayed: clientFormData.state.toLowerCase() == "done" ? "paid" : "unpaid",
        payedType: clientFormData.payedType,
        price: Number(orderSummary.price),
        shipping: Number(orderSummary.shipping),
        status: clientFormData.state,
        totalPrice: Number(orderSummary.totalPrice),
        userID: "anonymous",
      };
    };

  return (
    <>
      <AppComponentHeader
        cardStyle={{ height: 'auto' }}
        fullView
        title={messages['sidebar.management.AddOrders']}
      />
      <Box sx={{ mb: 6 }}>
        <AddUserInformation
          formData={clientFormData}
          setFormData={setClientFormData}
        />
      </Box>

      <AddOrderAddress
        formData={addressFormData}
        setFormData={setAddressFormData}
      />
      <Box sx={{ mb: 8 }}>
        <AddOrderCarts formData={cartFormData} setFormData={setCartFormData} setOrderSummary={setOrderSummary} />
      </Box>
      <Box sx={{ mb: 8 }}>
        <AddOrderReportPreviewContaner
          clientInfo={clientFormData}
          cartItem={cartFormData}
          orderSummary={orderSummary}
          addOrder={addOrder}
          orderId={orderId}
          orderAdded={orderAdded}
          loading={loading}
        />
      </Box>
      <AppInfoView />
    </>
  );
};
export default AddOrder;
