import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import PropTypes from 'prop-types';
import AddOrderReportPreviewContanerUI from './AddOrderReportPreviewContanerUI';

const AddOrderReportPreviewContaner = ({
  title,
  description,
  clientInfo,
  source,
  cartItem,
  orderSummary,
  addOrder,
  orderId,
  orderAdded,
  loading
}) => {
  const { messages } = useIntl();
  const [viewSource, setToggleViewSource] = useState(false);
  const [animation, setAnimation] = useState(false);


  return (
    <AddOrderReportPreviewContanerUI
      addOrder={addOrder}
      source={source}
      title={title}
      description={description}
      viewSource={viewSource}
      setToggleViewSource={setToggleViewSource}
      messages={messages}
      animation={animation}
      setAnimation={setAnimation}
      clientInfo={clientInfo}
      cartItem={cartItem}
      orderSummary={orderSummary}
      orderId={orderId}
      orderAdded={orderAdded}
      loading={loading}
    />
  );
};

AddOrderReportPreviewContaner.propTypes = {
  clientInfo: PropTypes.any.isRequired,
  component: PropTypes.any,
  source: PropTypes.any,
  title: PropTypes.any,
  description: PropTypes.node,
  cartItem: PropTypes.array,
  orderSummary: PropTypes.object,
  addOrder: PropTypes.func,
  orderId: PropTypes.any,
  orderAdded: PropTypes.bool,
  loading: PropTypes.bool
};

export default AddOrderReportPreviewContaner;
